/*
色サンプル

styleguideColorList

Colors:
$txtColor: #333
$red: #f00
$blue: #00f
$gray: #888
$black: #222
$test: rgba(250, 250, 250, .5)

Styleguide 1.1
============================================================*/
.styleguideColorList {
  margin: 0 0 0 -10px;
  display: flex;
  display: -webkit-flex;
}
.styleguideColorList_item {
  margin: 10px 0 0 10px;
  width: calc(100%/11 - 10px);
}
.styleguideColorList_item > a {
  display: block;
  text-decoration: none;
  text-align: center;
  word-break: break-all;
  text-align: center;
  color: inherit;
}
.styleguideColorList_item > a:before {
  content: "";
  display: block;
  padding-top: 100%;
  background: #000;
}
.styleguideColorList_item > a:after {
  display: none;
  text-align: center;
}
.styleguideColorList_item > a > span {
  display: block;
}

/*
/*レスポンシブ用　表示/非表示

.only-(sp/tab/pc)で.(sp/tab/pc)でのみ表示

.hidden-(sp/tab/pc)で.(sp/tab/pc)でのみ非表示

つまり.only-spでtab/pcは非表示に、.only-pcでtab/spは非表示に、

Markup:

Styleguide 1.2
============================================================*/
.hidden {
  display: none !important;
}

