/*
オーバーレイ

overlay

Markup:
<a href="javascript: void(0);" class="overlay"></a>

overlay-isOpen - open
overlay-isClose - close

Styleguide 3.2
============================================================*/
.overlay {
  display: block;
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  transition: 0.4s opacity;
  z-index: 1000;
}
.overlay-enter-active {
  display: block;
}
.overlay-enter {
  opacity: 0;
}
.overlay-enter:hover {
  opacity: 0;
}
.overlay-enter-to {
  opacity: 1;
}
.overlay-enter-to:hover {
  opacity: 1;
}
.overlay-leave-active {
  display: block;
}
.overlay-leave {
  opacity: 1;
}
.overlay-leave:hover {
  opacity: 1;
}
.overlay-leave-to {
  opacity: 0;
}
.overlay-leave-to:hover {
  opacity: 0;
}

