<label class="customInput"><input type="radio" name="" id=""><span class="customInput_inner">ああああああ</span></label>

Styleguide 4.11
============================================================*/
.customInput {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
  letter-spacing: normal;
}
.customInput_inner {
  overflow: hidden;
  position: relative;
  line-height: 1;
  vertical-align: middle;
}
.customInput_inner img {
  pointer-events: none;
}
.customInput > [type=checkbox], .customInput > [type=radio] {
  opacity: 0;
  position: absolute;
  top: -100%;
}
.customInput > [type=checkbox] + .customInput_inner, .customInput > [type=radio] + .customInput_inner {
  display: inline-block;
  vertical-align: middle;
  letter-spacing: normal;
  padding-left: calc(22px + .5em);
  text-indent: calc(-22px - .5em);
  line-height: 1.43;
}
.customInput > [type=checkbox] + .customInput_inner:before, .customInput > [type=radio] + .customInput_inner:before {
  content: "";
  text-indent: 0;
  border: 1px solid #a2a2a2;
  background: #fff;
  display: inline-block;
  vertical-align: middle;
  letter-spacing: normal;
  margin-right: 0.5em;
  width: 22px;
  height: 22px;
  border-radius: 4px;
  font-size: 18px;
  line-height: calc(22px - 2px);
  color: #333;
  text-align: center;
}
.customInput > [type=checkbox] + .customInput_inner:empty:before, .customInput > [type=radio] + .customInput_inner:empty:before {
  margin-right: 0;
}
.customInput > [type=checkbox]:checked + .customInput_inner:before {
  content: "";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
  line-height: calc(22px - 2px);
}
.customInput > [type=radio] + .customInput_inner:before {
  border-radius: 50%;
}
.customInput > [type=radio]:checked + .customInput_inner:before {
  background: #333;
  box-shadow: inset 0 0 0 2px #fff;
}

