.fileForm {
  margin: 20px 0 0;
  width: 100%;
}
.fileForm > :first-child {
  margin-top: 0;
}
.fileForm_btn {
  margin: 0;
  width: 154px;
}
.fileForm_ddComment {
  padding-left: 1em;
  text-indent: -1em;
  margin: 10px 0 0;
}
.fileForm_dropArea {
  width: 100%;
  margin: 5px 0 0;
  background: #eee;
  padding: 30px 20px;
  position: relative;
  border: 2px dashed transparent;
  transition: all 0.4s;
}
.fileForm_dropArea-isDragOver {
  border-color: #a2a2a2;
}
.fileForm_dropArea > :first-child {
  margin-top: 0;
}
.fileForm_dropTxt {
  text-align: center;
  margin: 0;
  opacity: 0.6;
}
.fileForm_list {
  margin: 0;
}
.fileForm_list > :first-child {
  margin-top: 0;
}
.fileForm_item {
  margin: 10px 0 0;
  display: flex;
  display: -webkit-flex;
  align-items: center;
  font-weight: bold;
  position: relative;
  overflow: hidden;
  word-break: break-all;
}
.fileForm_item [type=file] {
  position: absolute;
  top: -100%;
  opacity: 0;
}
.fileForm_item-template {
  display: none;
}
.fileForm_name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-decoration: none;
  color: inherit;
}
.fileForm_del {
  flex-grow: 0;
  flex-shrink: 0;
  display: block;
  text-decoration: none;
  color: #fff;
  text-align: center;
  width: 27px;
  height: 27px;
  line-height: 27px;
  font-size: 24px;
  background: #888;
  margin-right: 8px;
  font-weight: normal;
}
.fileForm_comment {
  margin: 0 0 0 8px;
  flex-grow: 0;
  flex-shrink: 0;
  width: 440px;
}

@media screen and (max-width: 736px) {
  .fileForm_item {
    flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    flex-direction: row;
  }
  .fileForm_item > span {
    width: calc(100% - 35px);
  }
  .fileForm_comment {
    margin: 5px 0 0;
    width: 100%;
  }
  .fileForm_ddComment {
    display: none;
  }
  .fileForm_dropArea {
    display: none;
  }
}
